<script>
  import Storage from '@/utils/localStorage'
  const reqUrl = {
    // 优惠券详情
    batchCreatePromInfo: "/member/erp/prom/batchCreatePromInfo",
    // 根据批量CommonCode查询
    listByCommonCodes: '/foundation/businessConfig/listByCommonCodes'
  }
  export default {
    data() {
      return {
        loading: false,
        detailParamMesg: {
          batchCreatePromId: ''
        },
        disCoupon: {}, // 满减券
        goodCoupon: {}, // 商品券
        disIsnull: true, // 满减券是否是空
        goodIsnull: true, // 商品券是否是空
        promType: '2',
        currentPromDiscount: {}, // 当前展示的卡券类型,
        erpPromotionType: []
      }
    },
    created() {
      this.getErpPromotionType()
      this.detailParamMesg.batchCreatePromId = this.$route.params.batchCreatePromId
      this.getDetail()
    },
    mounted() {
      
    },
    methods: {
      getErpPromotionType() {
        this.$http.post(reqUrl.listByCommonCodes, ['ErpPromotionType'], true).then((res) => {
          if (res.code === 0) {
            this.erpPromotionType = res.data['ErpPromotionType']
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log(e.message)
        })
      },
      triggerTypeFormater(triggerType) {
        // 1:活动;2:分享;3:签到;4:充值;111:天降红包
        switch(triggerType) {
          case 1:
            return '直接发放'
          break;
          case 4:
            return '充值返券(单次)'
          break;
          case 42:
            return '充值返券(多次)'
          break;
          case 71:
            return '消费发放'
          break;
          case 2:
            return '分享返券'
          break;
          case 3:
            return '签到返券'
          break;
          case 6:
            return '领取活动'
          break;
          case 81:
            return '扫码送券'
          break;
          case 82:
            return '扫码消费送券'
          break;
          case 91:
            return '省钱礼包'
          break;
          case 12:
            return '指定客户发券'
          break;
          case 111:
            return '天降红包'
          break;
          case 121:
            return '积分换券'
          break;
          default:
            return '其它券' 
          break;
        }
      },
      promTypeFormater(promType) {
        switch(promType) {
          case 2:
            return '满减券'
          break;
          case 5:
            return '商品券'
          break;
        }
      },
      ruleSourceFormater(ruleSource) {
        switch(ruleSource) {
          case 0:
            return '商城+门店商品'
          break;
          case 1:
            return '门店商品'
          break;
          case 2:
            return '商城商品'
          break;
        }
      },
      onlineGoodsTypeFormater(onlineGoodsType) {
        switch(onlineGoodsType) {
          case '3':
            return '预售商品'
          break;
          case '4':
            return '全国商品'
          break;
          case '5':
            return '拼团商品'
          break;
        }
      },
      onlineActivityTypeFormater(onlineActivityType) {
        switch(onlineActivityType) {
          case '11':
            return '拼团活动'
          break;
          case '21':
            return '秒杀活动'
          break;
          case '31':
            return '包装折扣活动'
          break;
          case '41':
            return '新人专享活动'
          break;
          case '51':
            return '预售活动'
          break;
          case '61':
            return '爆款活动'
          break;
        }
      },
      activityTypeFormater(activityType) {
        let type = activityType
        this.erpPromotionType.forEach((item) => {
          if(item.code == activityType) {
            type = item.value
          }
        })
        return type
      },
      goodsTypeFormater(goodsType) {
        switch(goodsType) {
          case 1:
            return '门店'
          break;
          case 2:
            return '商城'
          break;
        }
      },
      triggerSourceFormater(triggerSource) {
        switch(triggerSource) {
          case 1:
            return '单笔门店消费：'
          break;
          case 2:
            return '单笔商城消费：'
          break;
        }
      },
      triggerTimesTypeFormater(triggerTimesType) {
        // 0：无限制，1：整个活动只发放一次 ，2：每天一次，3：每周一次；
        switch(triggerTimesType) {
          case 0:
            return '无限制'
          break;
          case 1:
            return '整个活动只发放一次'
          break;
          case 2:
            return '每天一次'
          break;
          case 3:
            return '每周一次'
          break;
        }
      },
      /**
       * [getDetail 获取拼团活动详情]
       * @return {[type]} [description]
       */
      getDetail() {
        this.loading = true
        this.$http.post(reqUrl.batchCreatePromInfo, {batchCreatePromId: this.detailParamMesg.batchCreatePromId}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data) {
              let promInfoVos = res.data.promInfoVos
              promInfoVos.forEach(item => {
                if(item.promType == 2){
                  this.disCoupon = item
                }else if(item.promType == 5){
                  this.goodCoupon = item
                }
              })
              if(this.disCoupon.promDiscounts && this.disCoupon.promDiscounts.length > 0) {
                this.disCoupon.promDiscounts.forEach(item => {
                  for(let key in item) {
                    if(item[key] != '') {
                      this.disIsnull = false
                    }
                  }        
                })
              }
              if(this.goodCoupon.promDiscounts && this.goodCoupon.promDiscounts.length > 0) {
                this.goodCoupon.promDiscounts.forEach(item => {
                  for(let key in item) {
                    if(item[key] != '') {
                      this.goodIsnull = false
                    }
                  }        
                })
              }
              this.detailParamMesg = res.data
              if(this.disIsnull) {
                this.currentPromDiscount = this.goodCoupon
                this.promType = '5'
              }else if(this.goodIsnull) {
                this.currentPromDiscount = this.disCoupon
                this.promType = '2'
              }else {
                this.currentPromDiscount = this.disCoupon
                this.promType = '2'
              }
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      promTypeChange() {
        if(this.promType === '2') {
          this.currentPromDiscount = this.disCoupon
        }else if(this.promType === '5') {
          this.currentPromDiscount = this.goodCoupon
        }
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }

</script>


